.App {
  text-align: center;
  padding-top: 64px;
}

h2 {
  color: rgba(255,255,255,0.7);
  text-align: center;
  font-size: 22px;
}

h2 em {
  font-weight: bold;
  color: #fff;
  font-style: normal;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
